/**
 * Main application routes
 */

'use strict';

import errors from './components/errors';
import path from 'path';

export default function(app) {
  // Insert routes below
  app.use('/api/server', require('./api/server'));
  app.use('/api/memos', require('./api/memo'));
  app.use('/api/media', require('./api/media'));
  app.use('/api/roomTimelines', require('./api/roomTimeline'));
  app.use('/api/timelineTasks', require('./api/timelineTask'));
  app.use('/api/masterTimeline', require('./api/masterTimeline'));
  app.use('/api/baseObject', require('./api/baseObject'));
  app.use('/api/things', require('./api/thing'));
  app.use('/api/users', require('./api/user'));
  app.use('/api/site', require('./api/site'));

  app.use('/auth', require('./auth').default);

  // All undefined asset or api routes should return a 404
  app.route('/:url(api|auth|components|app|bower_components|assets)/*')
   .get(errors[404]);

  // All other routes should redirect to the index.html
  app.route('/*')
    .get((req, res) => {
      res.sendFile(path.resolve(app.get('appPath') + '/index.html'));
    });
}
